	module.exports = {"data":{"site":{"siteMetadata":{"title":":-)","author":"github.com/9renpoto"}},"markdownRemark":{"id":"/home/travis/build/9renpoto/entry/src/pages/2017-08-26-lerna/index.md absPath of file >>> MarkdownRemark","html":"<h1>TL;DR</h1>\n<ul>\n<li>lernaは大きすぎないpackageを複数管理するときに割りと便利</li>\n<li>\n<p>lernaで便利なものと、対応していないものがある</p>\n<ul>\n<li>flow-typed, etc</li>\n</ul>\n</li>\n</ul>\n<h2>Lerna</h2>\n<ul>\n<li>babel等で使われており存在は知っていたが、プラグイン等を管理するようなpackageを持っておらず使うつもりはなかった</li>\n<li>\n<p>greenkeeperに対応していない</p>\n<ul>\n<li>issueはあがっている</li>\n</ul>\n</li>\n<li>\n<p>releaseコマンドを普段使わない</p>\n<ul>\n<li>git master commitの発生が気に食わない</li>\n<li>素直にpublishだけしてほしい</li>\n</ul>\n</li>\n<li>\n<p>それでも環境を分ける場合が増えてきたので使ってみる</p>\n<ul>\n<li>eslintrc</li>\n<li>babelrc</li>\n<li>tsconfig</li>\n</ul>\n</li>\n</ul>\n<h2>入れてみた</h2>\n<ul>\n<li><a href=\"https://github.com/9renpoto/js\">https://github.com/9renpoto/js</a></li>\n<li><a href=\"https://github.com/9renpoto/ts\">https://github.com/9renpoto/ts</a></li>\n</ul>\n<h2>勉強</h2>\n<ul>\n<li>先を見るために今を見る</li>\n<li>\n<p>今の何が大事かは、最初はテスト環境だと思っている</p>\n<ul>\n<li>テストが走る</li>\n<li>ライブラリ特有のテストがかける</li>\n<li>jsx,tsx</li>\n<li>vue</li>\n<li>カバレッジが取れる</li>\n<li>現実的な生産性が見える</li>\n</ul>\n</li>\n</ul>\n<p><img src=\"https://media.giphy.com/media/26tPplGWjN0xLybiU/giphy.gif\" alt=\"gif\"></p>","frontmatter":{"title":"フロントエンド勉強環境にlernaを使う","date":"August 26, 2017"}}},"pathContext":{"path":"/entry/2017/08/28/lerna/"}}

